.searchInput{
    width: 300px;
    vertical-align: middle;
    white-space: nowrap;
    position: relative;
  }
  
  .searchInput input#search{
    width: 40px;
    height: 40px;
    background-image: linear-gradient( 135deg, #EE9AE5 10%, #5961F9 100%);

	border-radius: 50%;
    border: none;
    font-size: 10pt;
    float: left;
    color: #262626;
    padding-left: 40px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 20px;
    color: #fff;
	box-shadow: 0px 1px 15px 1px rgba(69, 65, 78, 0.08);
    -webkit-transition: width .55s ease;
    -moz-transition: width .55s ease;
    -ms-transition: width .55s ease;
    -o-transition: width .55s ease;
    transition: width .55s ease;
  }
  
  .searchInput input#search::-webkit-input-placeholder {
     color: #fff;
  }
   
  .searchInput input#search:-moz-placeholder { /* Firefox 18- */
     color: #fff;  
  }
   
  .searchInput input#search::-moz-placeholder {  /* Firefox 19+ */
     color: #fff;  
  }
   
  .searchInput input#search:-ms-input-placeholder {  
     color: #fff;  
  }
  
  .searchInput .icon{
    position: absolute;
    top: 50%;
    margin-left: 12px;
    margin-top: 12px;
    z-index: 1;
    display: inline-flex;
	align-items: center;
	justify-content: center;
    color: #4f5b66;
  }
  
  .searchInput input#search:focus, .searchInput input#search:active{
    outline:none;
    width: 280px;
  }
   
  .searchInput:hover input#search{
  width: 280px;
  }
   
  .searchInput:hover .icon{
    color: #fff;
  }

  .list-title{
      margin-bottom: .5em;
  }
  .list-value{
    margin-bottom: .5em;
    font-weight: bold;
}
